import { mapState,mapMutations} from 'vuex'
export default {
	props:{
		title:{
			type:Object
		}
	},
	computed:{
		...mapState({
			msgType:state=>state.msgType
		})
	},
	methods: {
		...mapMutations(['getMsgType']),
		sendType(type){
			this.getMsgType({ type })
		}
	}
}
